<template>
  <div class="box">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="">
        <el-input v-model="form.title"></el-input>
      </el-form-item>
      <el-form-item label="">
        <div>
          <el-input
            placeholder=""
            v-model="problemid"
            clearable
            style="width: 40%"
          ></el-input>
          <el-input v-model="score" placeholder="" style="width: 10%"></el-input>
          <el-button
            slot="append"
            icon="el-icon-download"
            @click="addProblemId"
          ></el-button>
        </div>

        <el-table :data="problemlist" style="width: 40%">
          <el-table-column prop="id" label=""> </el-table-column>
          <el-table-column prop="score" label=""> </el-table-column>
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <!--  -->
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click.native.stop="deleteproblem(scope.$index)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="form.start"
          style="width: 300px"
          type="date"
          placeholder=""
          format="yyyy  MM  dd  H  m  s "
          value-format="yyyy-MM-dd HH:mm:ss"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="form.end"
          style="width: 300px"
          type="date"
          placeholder=""
          format="yyyy  MM  dd  H  m  s "
          value-format="yyyy-MM-dd HH:mm:ss"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <mavon-editor v-model="form.info" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="putRace"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        class_id: "",
        title: "",
        info: "",
        start: "",
        end: "",
        prob_list: "",
      },
      // 
      problemid: "",
      score: "",
      problemlist: [],
    };
  },
  methods: {
    // 
    async putRace() {
      if (this.problemlist.length <= 0) {
        return this.$message.error("");
      }
      this.form.prob_list = `${this.problemlist[0].id},${this.problemlist[0].score}`;
      for (var i = 1; i < this.problemlist.length; i++) {
        this.form.prob_list = `${this.form.prob_list}|${this.problemlist[i].id},${this.problemlist[i].score}`;
      }
      //console.log(this.form.problem_sets);
      this.form.class_id = this.$route.query.id;
      //console.log(this.form)
      const { data: res } = await this.$http.post(
        "homework/new_homework",
        this.form
      );
      //console.log(res);
      if (res.meta.status === 400) {
        return this.$message.error(res.meta.message);
      }
      this.$message.success(res.meta.message);
      this.$router.push("/classhome");
    },
    async addProblemId() {
      if(this.score.length<=0){
          return this.$message.error("");
      }
      const { data: res } = await this.$http.get(
        `problems/prob_exists?num=${this.problemid}`
      );
      if (res.meta.status === 400) {
        return this.$message.error("");
      }
      let newlist = {
        id: "",
        score: ""
      };
      newlist.id = this.problemid;
      newlist.score = this.score;
      this.problemlist.push(newlist);
    },
    // 
    deleteproblem(index) {
      //console.log(index)
      this.problemlist.splice(index, 1);
      //console.log(this.problemlist)
    },
  },
};
</script>

<style scoped>
.box {
  margin-left: 10%;
  margin-top: 20px;
  width: 80%;
}
</style>
