<template>
  <div class="box">
    <el-form :rules="rules" ref="form" :model="form" label-width="80px">
      <el-form-item label="" prop="num">
        <el-input v-model="form.num"></el-input>
      </el-form-item>
      <el-form-item label="" prop="title">
        <el-input v-model="form.title"></el-input>
      </el-form-item>
      <el-form :rules="rules" :inline="true" ref="form" :model="form" label-width="80px">
        <el-form-item label="" prop="difficulty">
          <el-select v-model="form.difficulty" clearable placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="time_limit">
          <el-input v-model="form.time_limit">
            <template slot="append">ms</template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="memory_limit">
          <el-input v-model="form.memory_limit">
            <template slot="append">MB</template>
          </el-input>
        </el-form-item>
      </el-form>
      <el-form-item label="" prop="is_public">
        <el-radio-group v-model="form.is_public">
          <el-radio label="1"></el-radio>
          <el-radio label="0"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="">
        <mavon-editor v-model="form.content" :toolbars="markdownOption" />
      </el-form-item>
      <el-button type="primary" @click="addpb"></el-button>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      form: {
        num: "",
        title: "",
        difficulty: "1",
        memory_limit: "128",
        time_limit: "1000",
        content: "",
        is_public: "1",
      },
      // 
      options: [
        {
          value: "1",
          label: "",
        },
        {
          value: "2",
          label: "",
        },
        {
          value: "3",
          label: "",
        },
        {
          value: "4",
          label: "",
        },
        {
          value: "5",
          label: "",
        },
      ],
      markdownOption: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: false, // 
        imagelink: false, // 
        code: false, // code
        table: false, // 
        fullscreen: true, // 
        readmodel: true, // 
        htmlcode: false, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: false, // eventssave
        /* 1.4.2 */
        navigation: false, // 
        /* 2.1.8 */
        alignleft: false, // 
        aligncenter: false, // 
        alignright: false, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: true, // 
      },
      rules: {
        num: [
          { required: true, message: '', trigger: "blur" },
          {
            pattern: /^[0-9A-Za-z]{2,10}$/,
            message: "(2~10)", trigger: "blur"
          }],
        title: [
          { required: true, message: '', trigger: "blur" },
        ],
        memory_limit: [
          { required: true, message: '', trigger: "blur" },
           {
            pattern: /^[0-9]{1,10}$/,
            message: "", trigger: "blur"
          }
        ],
        time_limit: [
          { required: true, message: '', trigger: "blur" },
           {
            pattern: /^[0-9]{1,10}$/,
            message: "", trigger: "blur"
          }
        ],
      },
    };
  },
  methods: {
    change(value, render) {
      this.form.content = render;
    },
    addpb() {
    this.$refs.form.validate(async (valid) => {
    if (!valid) return;
    const { data: res } = await this.$http.post(
        "problems/new_problem",
        this.form
      );
      //console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.$router.push('/topic')  
      })   
    },
  },
};
</script>
<style scoped>
.box {
  width: 80%;
  padding-left: 10%;
  padding-top: 20px;
}
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 600px;
}
</style>
