<template>
  <div class="box">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="">
        <el-input v-model="form.title"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input placeholder="" v-model="problemid" clearable>
          <el-button
            slot="append"
            icon="el-icon-download"
            @click="addProblemId"
          ></el-button>
        </el-input>
        <el-table :data="problemlist" style="width: 40%">
          <el-table-column prop="num" label=""> </el-table-column>
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <!--  -->
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click.native.stop="deleteproblem(scope.$index)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="form.start"
          style="width: 300px"
          type="date"
          placeholder=""
          format="yyyy  MM  dd  H  m  s "
          value-format="yyyy-MM-dd HH:mm:ss"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="form.end"
          style="width: 300px"
          type="date"
          placeholder=""
          format="yyyy  MM  dd  H  m  s "
          value-format="yyyy-MM-dd HH:mm:ss"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-radio-group v-model="form.contest_type">
          <el-radio label="1">ICPC</el-radio>
          <el-radio label="2">OI</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="">
        <mavon-editor v-model="form.info" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="putRace"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        title: "",
        problem_sets: "",
        start: "",
        end: "",
        manager: "",
        contest_type: "",
        info: "",
        id: ""
      },
      // 
      problemid: "",
      problemlist: [],
    };
  },
  created() {
    this.getRaceList();
  },
  methods: {
    // 
    async getRaceList() {
      const { data: res } = await this.$http.get(
        `contest/contest?id=${this.$route.query.id}`
      );
      //console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.form.title = res.data.title;
      this.problemlist = res.data.prob_list;
      this.form.start = res.data.start_time;
      this.form.end = res.data.end_time;
      this.form.contest_type = res.data.contest_type.toString();
      this.form.info = res.data.information;
    },
    // 
    async putRace() {
      if (this.problemlist.length <= 0) {
        return this.$message.error("");
      }
      this.form.problem_sets = `${this.problemlist[0].num}`;
      for (var i = 1; i < this.problemlist.length; i++) {
        this.form.problem_sets = `${this.form.problem_sets}|${this.problemlist[i].num}`;
      }
      //console.log(this.form.problem_sets);
      this.form.manager = window.localStorage.getItem("userid");
      //console.log(this.form);
      this.form.id = this.$route.query.id
      const { data: res } = await this.$http.post(
        "contest/update_contest",
        this.form
      );
      //console.log(res);
      if (res.meta.status === 400) {
        return this.$message.error("");
      }
      this.$message.success("");
    },

    async addProblemId() {
      const { data: res } = await this.$http.get(
        `problems/prob_exists?num=${this.problemid}`
      );
      if (res.meta.status === 400) {
        return this.$message.error("");
      }
      let newlist = {
        num: "",
      };
      newlist.num = this.problemid;
      this.problemlist.push(newlist);
    },

    // 
    deleteproblem(index) {
      //console.log(index)
      this.problemlist.splice(index, 1);
      //console.log(this.problemlist)
    },
  },
};
</script>

<style scoped>
.box {
  margin-left: 10%;
  margin-top: 20px;
  width: 80%;
}
</style>
