<template>
  <div class="topicbox">
    <!--  -->
    <el-row :gutter="20">
      <el-col :span="8">
        <el-input
          placeholder=""
          v-model="queryInfo.query"
          clearable
          @clear="getUserList"
        >
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="getproblemList"
          ></el-button>
        </el-input>
      </el-col>
      <el-col :span="4">
        <el-button
          type="primary"
          @click="addDialogVisible"
          v-if="userlevel == 1"
          ></el-button
        >
      </el-col>
    </el-row>
    <!--  -->
    <el-table
      :data="problemslist"
      stripe
      class="problemlist"
      @row-click="gotosubmit"
    >
      <el-table-column label="" prop="ac" width="100" align="center">
        <template slot-scope="scope">
          <i class="el-icon-check" v-if="scope.row.ac"></i>
        </template>
      </el-table-column>
      <el-table-column label="" prop="num" width="100"></el-table-column>
      <el-table-column label="" prop="title"></el-table-column>
      <el-table-column label="" prop="difficulty" width="50"></el-table-column>
      <el-table-column label="" prop="pass" width="70"></el-table-column>
      <el-table-column label="" prop="submit" width="70"></el-table-column>
      <el-table-column label="" prop="role_name" width="70"></el-table-column>
      <el-table-column label="" width="100" v-if="userlevel == 1">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.mg_state"
            @change="userStateChanged(scope.row)"
          >
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" width="400" v-if="userlevel == 1">
        <template slot-scope="scope">
          <!--  -->
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click.native.stop="showEditDialog(scope.row.num)"
          ></el-button>
          <!--  -->
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click.native.stop="removeProblemById(scope.row.num)"
          ></el-button>
          <!--  -->
          <el-button
            type="primary"
            size="mini"
            @click.native.stop="upload(scope.row.num)"
            ><i class="el-icon-upload el-icon--right"></i
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-size="queryInfo.pagesize"
      layout="total, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <input type="file" @change="getFile($event)" />
      <button @click="uploadsubmit($event)"></button>
    </el-dialog>
  </div>
</template>

<script>
import "../Topic/style.js";
export default {
  data() {
    return {
      // 
      queryInfo: {
        query: "",
        // 
        pagenum: 1,
        // 
        pagesize: 2,
      },
      problemslist: [],
      total: 0,
      form: {
        num: "",
      },
      // 
      dialogVisible: false,
      // 
      uploadfile: "",
      uploadnum: "",
      // 
      userlevel: "0",
    };
  },
  created() {
    this.getUserList();
    this.getPageinfo();
    this.getuserlevel();
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$http.get(
        "problems/list?page_no=" + this.queryInfo.pagenum
      );
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.problemslist = res.data;
      //console.log(res)
    },
    // 
    async getPageinfo() {
      const { data: res } = await this.$http.get("problems/page_info");
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.total = res.count;
      this.queryInfo.pagesize = res.show_per_page;
    },
    //  pagesize 
    handleSizeChange(newSize) {
      // console.log(newSize)
      this.queryInfo.pagesize = newSize;
      this.getUserList();
    },
    //   
    handleCurrentChange(newPage) {
      // console.log(newPage);
      this.queryInfo.pagenum = newPage;
      this.getUserList();
    },
    // 
    getuserlevel() {
      if (window.localStorage.getItem("access") === '0') this.userlevel = 1;
    },
    // 
    async getproblemList() {
      console.log(this.queryInfo.query);
      const { data: res } = await this.$http.get(
        "problems/find_problems/?title_or_num=" + this.queryInfo.query
      );
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.problemslist = res.data;
    },
    // 
    gotosubmit(row) {
      //console.log(1)
      this.$router.push({ path: "/submit", query: { id: row.num } });
    },

    //  switch 
    /* async userStateChanged(userinfo) {
      console.log(userinfo)
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      )
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state
        return this.$message.error('')
      }
      this.$message.success('')
    }, */
    // 
    showEditDialog(num) {
      this.$router.push({ path: "/revise", query: { id: num } });
    },
    // Id
    async removeProblemById(id) {
      // 
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);

      //  confirm
      //  cancel
      // console.log(confirmResult)
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      this.form.num = id;
      // console.log(this.form.num);
      const { data: res } = await this.$http.post(
        "problems/del_problem",
        this.form
      );
      // console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.getUserList();
      this.getPageinfo();
    },
    // 
    getFile(event) {
      this.uploadfile = event.target.files[0];
      //console.log(this.uploadfile);
    },
    async uploadsubmit(event) {
      event.preventDefault(); //
      // formData 
      var formData = new FormData();
      //  formData 
      formData.set("num", this.uploadnum);
      formData.append("file", this.uploadfile);
      //console.log(formData)
      const { data: res } = await this.$http.post(
        "problems/upload_cases",
        formData
      );
      //console.log(res)
    },
    upload(id) {
      this.uploadnum = id;
      this.dialogVisible = true;
    },

    // 
    addDialogVisible() {
      this.$router.push("/Addproblems");
    },
  },
};
</script>

<style scoped>
.topicbox {
  width: 80%;
  margin-top: 40px;
  margin-left: 160px;
}
.problemlist {
  margin-top: 40px;
}
</style>
