<template>
  <div class="box">
    <el-form :rules="rules" ref="form" :model="form" label-width="80px">
      <el-form-item label="" prop="class_name">
        <el-input v-model="form.class_name"></el-input>
      </el-form-item>
      <el-form-item label="id" prop="teacher_id">
        <el-input v-model="form.teacher_id"></el-input>
      </el-form-item>
      <el-form-item label="">
        <mavon-editor v-model="form.intro" :toolbars="markdownOption" />
      </el-form-item>
      <el-button type="primary" @click="addclass" class="button"></el-button>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      form: {
        class_name: "",
        intro: "",
        teacher_id: "",
      },
      markdownOption: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: false, // 
        imagelink: false, // 
        code: false, // code
        table: false, // 
        fullscreen: true, // 
        readmodel: true, // 
        htmlcode: false, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: false, // eventssave
        /* 1.4.2 */
        navigation: false, // 
        /* 2.1.8 */
        alignleft: false, // 
        aligncenter: false, // 
        alignright: false, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: true, // 
      },
       rules: {
        class_name: [
          { required: true, message: '', trigger: "blur" },
        ],
        teacher_id: [
          { required: true, message: 'id', trigger: "blur" },
        ],
       }
    };
  },
  methods: {
    addclass() {
      this.$refs.form.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.post(
          "class/create_class",
          this.form
        );
        console.log(res);
        if (res.meta.status !== 200) {
          return this.$message.error("");
        }
        this.$message.success("");
        this.$router.push("/class");
      });
    },
  },
};
</script>
<style scoped>
.box {
  width: 80%;
  padding-left: 10%;
  padding-top: 20px;
}
.button {
    margin-left: 80px;
}
</style>