<template>
    <div>
      <mavon-editor v-model="vlaue" :toolbars="markdownOption" @save="save" />
    </div>
</template>
<script>
export default {
    data() {
        return {
            form: {
               num: '',
               data: '',
            },
            vlaue: '',
         markdownOption: {
               bold: true, // 
               italic: true, // 
               header: true, // 
               underline: true, // 
               strikethrough: true, // 
               mark: true, // 
               superscript: true, // 
               subscript: true, // 
               quote: true, // 
               ol: true, // 
               ul: true, // 
               link: false, // 
               imagelink: false, // 
               code: false, // code
               table: false, // 
               fullscreen: true, // 
               readmodel: true, // 
               htmlcode: false, // html
               help: true, // 
               /* 1.3.5 */
               undo: true, // 
               redo: true, // 
               trash: true, // 
               save: true, // eventssave
               /* 1.4.2 */
               navigation: false, // 
               /* 2.1.8 */
               alignleft: false, // 
               aligncenter: false, // 
               alignright: false, // 
               /* 2.2.1 */
               subfield: true, // 
               preview: true, // 
            }          
        }
    },
    methods: {
        change(value, render) {
          this.form.data = render;
        },
         async save() {
           this.form.num = this.$route.query.id
           this.form.data = this.vlaue
           //console.log(this.form)
           const { data: res } = await this.$http.post('problems/write_problem', this.form)
           //console.log(res)
           if (res.meta.status !== 200) {
             return this.$message.error('')
           }
           this.$message.success("");
           this.$router.push('/topic')
           },
         }
}
</script>
<style scoped>
</style>
