<template>
  <div class="box">
    <!--  -->
    <el-row :gutter="20">
      <el-col :span="8">
        <el-input placeholder="" v-model="query" clearable>
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </el-col>
      <el-col :span="8">
        <el-button type="primary" @click="addDialogVisible" v-if="userlevel"></el-button>
        <el-button type="success" @click="student_add_class"
          ></el-button
        >
      </el-col>
    </el-row>
    <el-table
      :data="classlist"
      style="width: 100%"
      @row-click="gotoClassHome"
      class="classlist"
    >
      <el-table-column prop="class_id" label="" width="180">
      </el-table-column>
      <el-table-column prop="class_name" label="" width="180">
      </el-table-column>
      <el-table-column
        prop="intro"
        label=""
        width="500"
        :show-overflow-tooltip="true"
      >
      </el-table-column>
      <el-table-column label="" v-if="userlevel">
        <template slot-scope="scope">
          <!--  -->
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click.native.stop="reviseClass(scope.row.class_id)"
          ></el-button>
           <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click.native.stop="del_class(scope.row.class_id)"
            ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%"
    >
      <el-form label-width="80px">
        <el-form-item label="ID">
          <el-input v-model="addourclass.class_id"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addourclass.invite_code"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="join_class"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      classlist: [],
      //
      query: "",
      userlevel: false,
      addourclass: {
        class_id: "",
        invite_code: "",
      },
      // 
      dialogVisible: false,
      del_class_from: {
        class_id: ""
      },
    };
  },
  created() {
    this.getClassList();
    this.getuserlevel();
  },
  methods: {
    async getClassList() {
      const { data: res } = await this.$http.get("class/home");
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      //console.log(res);
      this.classlist = res.data;
    },
    // 
    addDialogVisible() {
      this.$router.push("/addclass");
    },
    // 
    gotoClassHome(row) {
      this.$router.push({ path: "/classhome", query: { id: row.class_id } });
    },
    // 
    getuserlevel() {
      if (window.localStorage.getItem("access") <=1 ) this.userlevel = true;
    },
    // 
    reviseClass(class_id) {
      this.$router.push({ path: "/reviseclass", query: { id: class_id } });
    },
    // 
    student_add_class() {
      this.dialogVisible = true;
    },
    async join_class() {
      const { data: res } = await this.$http.post("class/join_class", this.addourclass);
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.message);
      }
      this.$message.success(res.meta.message);
      this.dialogVisible = false;
    },

     // Id
    async del_class(id) {
      // 
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);

      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      // console.log(this.form.num);
      this.del_class_from.class_id = id;
      const { data: res } = await this.$http.post(
        "class/delete_class",
        this.del_class_from
      );
       //console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.message);
      }
      this.$message.success("");
      this.getClassList();
    },
  },
};
</script>
<style scoped>
.box {
  width: 80%;
  margin-top: 40px;
  margin-left: 160px;
}
.classlist {
  margin-top: 40px;
}
</style>